<template>
  <q-page>
    <div v-if="!isBackendAvailable">
      <backend-status></backend-status>
    </div>
    
    <q-card v-if='isBackendAvailable' flat>
      <q-card-section>
        <q-list bordered>
          <q-item>
            <q-item-section avatar>
              <q-icon name="fa-solid fa-music" />
            </q-item-section>

            <q-item-section>
              <q-item-label>Preset-Vorauswahl</q-item-label>
            </q-item-section>

            <q-item-section side>
              <q-btn no-caps color="primary" @click="selectPresets" class="q-mb-md" :label="selectedPresetCollectionName"/>
            </q-item-section>
          </q-item>
          <q-item disable>
            <q-item-section avatar>
              <q-icon name="light" />
            </q-item-section>

            <q-item-section>
              <q-item-label>Ambient-Beleuchtung</q-item-label>
            </q-item-section>

            <q-item-section side>
              <q-toggle size="xl" v-model="ambientLightOn"></q-toggle>
            </q-item-section>
          </q-item>
        </q-list>
      </q-card-section>

      <q-card-section>
        <q-list bordered>
          <q-item disable>
            <q-item-section avatar>
              <q-icon name="door_front" />
            </q-item-section>

            <q-item-section>
              <q-item-label>Zugangstür ist {{  roomDoorOpen ? "offen" : "verschlossen" }}</q-item-label>
            </q-item-section>

            <q-item-section side>
              <q-btn color='primary' round size='xl' :icon='roomDoorOpen ? "lock" : "lock_open"'></q-btn>
            </q-item-section>
          </q-item>
          </q-list>
      </q-card-section>

      <q-card-section>
        <q-list bordered>
          <q-item>
            <q-item-section avatar>
              <q-icon name="light_mode" />
            </q-item-section>

            <q-item-section>
              <q-item-label>Handy-Display nicht abdunkeln</q-item-label>
              <q-item-label caption lines="2">Reduziert die Akkulaufzeit</q-item-label>
            </q-item-section>

            <q-item-section side>
              <q-toggle size='xl' v-model="isPreventScreenDimming"></q-toggle>
            </q-item-section>
          </q-item>
          </q-list>
      </q-card-section>

      <q-card-section class="q-mt-lg">
        <q-btn no-caps :loading='performDoorAction' color="secondary" @click="performRoomReset" class="full-width q-mb-md">
          Proberaum zurücksetzen
          <template v-slot:loading>
            <q-spinner class="on-left" />
            Proberaum wird zurückgesetzt...
          </template>
        </q-btn>
      </q-card-section>
    </q-card>
  </q-page>
</template>


<script>
import { resetRoom } from '@/services/apiServices';
import { useInstrumentStore } from '@/stores/instruments-store';
import { computed } from 'vue'
import BackendStatus from '@/components/BackendStatus.vue';
import { useRouter } from 'vue-router';

export default {
  name: 'RoomView',
  components: {
    BackendStatus,
  },
  setup() {
    const store = useInstrumentStore();
    const router = useRouter();

    const selectedPresetCollectionName = computed(() => store.getSelectedPresetCollectionName);
    const isBackendAvailable = computed(() => store.isBackendAvailable);

    const roomDoorOpen = computed(() => store.roomStatus.door.status === 'open');
    const ambientLightOn = computed(() => store.roomStatus.light.is_on);

    const selectPresets = () => {
      router.push('/select-presets');
    }

    if (store.isBackendAvailable) {
      store.updateStore();
    }

    const isPreventScreenDimming = computed({
      get: () => store.isPreventScreenDimming,
      set: (newValue) => {
        store.setPreventScreenDimming(newValue);
      }
    });

    return { 
      selectedPresetCollectionName,
      roomDoorOpen,
      ambientLightOn,
      isBackendAvailable,
      selectPresets,
      isPreventScreenDimming,
    };
  },
  data() {
    return {
      performDoorAction: false,
    };
  },
  methods: {
    openPresetCollectionSelector() {
      this.$refs.presetSelector.show();
    },
    performRoomReset() {
      const store = useInstrumentStore();

      this.performDoorAction = true;
      // TODO: we shall wait on the feedback from the call to the REST api
      setTimeout(() => {
        this.performDoorAction = false;
      }, 1500)

      resetRoom()
        .then(response => {
          console.log('Room reset:', response);
        })
        .catch(error => {
          console.error('Error resetting room:', error);
        });

      store.updateStore();
    }
  }
};
</script>